import { Component,OnInit } from '@angular/core';

import { OnActivate,CanDeactivate,RouteSegment,RouteTree  } from '@angular/router';

import { Todo } from './data-class/todo';
import { TodoService } from './service/todo.service';
import { TodoListComponent } from './todo-list/todo-list.component';
import { TodoFormComponent } from './todo-form/todo-form.component';
import { RemainArchiveComponent } from './remain-archive/remain-archive.component';

import {PageScroll} from '../page-scroll-directive/page-scroll.directive';
import {IEasingFunction} from '../page-scroll-directive/page-scroll-config';

@Component({
    selector:'todo-portal',
    templateUrl:"app/todo-portal/todo-portal.component.html",
    directives:[TodoListComponent,TodoFormComponent,RemainArchiveComponent,
                PageScroll
               ],
    providers:[TodoService]
})

export class TodoPortalComponent implements OnInit {
    title:string="Todo Portal";
    todos:Todo[];
    
    constructor(private _tS:TodoService){
    }
    ngOnInit(){
        this._tS.getTodoSlowly().then(tds=> this.todos=tds );
    }
    checkTask(index:number){
        this.todos[index].done=!this.todos[index].done;
        this.todos=[].concat(this.todos);
    }
    addNewTodo(todo:Todo){
        this.todos=this.todos.concat(todo);
    }
    deleteDoneTask(tds:Todo[]){
        this.todos=this.todos.filter(todo=>!todo.done);
        this.todos=[].concat(this.todos);
    }
    //----------------------------------//
    myEasing:IEasingFunction = (t:number, b:number, c:number, d:number):number => {
        // easeInOutExpo easing
        if (t === 0) return b;
        if (t === d) return b + c;
        if ((t /= d / 2) < 1) return c / 2 * Math.pow(2, 10 * (t - 1)) + b;
        return c / 2 * (-Math.pow(2, -10 * --t) + 2) + b;
    };
    //-----------------------------------
     routerOnActivate(curr: RouteSegment, prev?: RouteSegment, 
                     currTree?: RouteTree, prevTree?: RouteTree) : void{
           console.log("inside Todo OnActivate");
           setTimeout(function() {
               console.log("inside OnActivate setTimeout");
           }, 2000);
    }
    modelStatus:any;
    routerCanDeactivate(currTree?: RouteTree, futureTree?: RouteTree) : Promise<boolean>{
        console.log("inside todo CanDeactivate");
              return new Promise((res, rej) => {
                        setTimeout(() => {
                            res('Now ready.');
                        }, 5000);
                        }).then((r) =>  this.modelStatus = r );              
                        }
}